.carousel
{

  &__inner
  {
    display: flex;
    position: relative;
    overflow: hidden;
    margin: 2rem auto;
    width: 640px;
    height: 426px;
    text-align: center;

    & div
    {
      position: absolute;
      top: 0;
      left: 0;
      // width: 100%;
      // height: 100%;
      opacity: 0;
      transition: all 0.3s linear;

      &.activeSlide
      {
        opacity: 1;
        transform: translateX(0);
      }

      &.lastSlide
      {
        transform: translateX(-100%);
      }

      &.nextSlide
      {
        transform: translateX(100%);
      }
    }
  }

  // &__item
  // {
  //   position: absolute;
  //   top: 0;
  //   left: 0;
  //   width: 100%;
  //   height: 100%;
  //   // opacity: 0;
  //   transition: all 0.3s linear;
  // }
}

.prev, .next
{
  position: absolute;
  top: 20rem;
  transform: translateY(-50%);
  display: grid;
  place-items: center;
  font-size: 1.4rem;
  cursor: pointer;
}

.prev
{
  left: 0;
}

.next
{
  right: 0;
}